@import url(https://fonts.maateen.me/solaiman-lipi/font.css);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
html,
body {
  height: 100% !important;
  overflow: hidden !important;
  margin: 0 !important;
  font-family: "SolaimanLipi", sans-serif;
  font-size: 13px;
  font-weight: 300 !important;
  padding: 0 !important;
  color: #000 !important;
  background: #fff !important;
}

a {
  text-decoration: none;
}
.leaflet-container {
  width: 100%;
  height: 100vh;
}
:root {
  --main-red: #c60b1e;
  --main-light-gray: #fafafa;
  --main-orange: #ff7400;
  --orange-border: #ff740080;
}
.bg-fff {
  background: #fff;
}
.fr-box {
  display: flex;
  height: 100%;
  flex-direction: column;
}

.fr-wrapper {
  flex: 1 1;
}

.fr-element {
  min-height: 100%;
}

.btn-violate {
  background-color: #683392;
  color: #fff;
}

.btn-violate:hover {
  color: #fff;
  background-color: #8c47c3;
  border-color: #8c47c3;
}

.bg-light-n {
  background: #fafafa;
}

.cursor-pointer {
  cursor: pointer;
}

.full_screen {
  min-width: 100%;
  min-height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #fff;
  z-index: 99999;
}
.font-16 {
  font-size: 16px !important;
}
.font-14 {
  font-size: 14px !important;
}
.font-13 {
  font-size: 13px !important;
}
.font-12 {
  font-size: 12px !important;
}
.font-violate {
  color: #683392;
}
.font-weight-1 {
  text-shadow: 0 0 1px;
}
.ant-table-tbody tr:nth-child(odd) {
  background: #f5f8f7;
}
.btn.btn-outline-secondary {
  color: #494988;
  border-color: #d9d9d9;
}
.btn-outline-secondary.active,
.btn-outline-secondary:active,
.btn-outline-secondary:hover,
.btn-outline-secondary:focus {
  background-color: #e1e1ef !important;
  border-color: #d9d9d9 !important;
  color: #494988 !important;
  box-shadow: none;
}
.btn-pink,
.btn-outline-pink:hover,
.btn-outline-pink:active,
.btn-outline-pink:focus {
  background-color: #fd397a !important;
  color: white !important;
}
.btn-pink:hover,
.btn-pink:active,
.btn-outline-pink {
  background-color: #fd397a20 !important;
  color: #fd397a !important;
  border-color: #fd397a80 !important;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab-active {
  border-top: 1.5pt solid #683392 !important;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
  padding: 0 8px !important;
  line-height: 32px !important;
}
.anticon {
  vertical-align: 0 !important;
}

.Apps {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #6c88bf;
}

#search-container{display:flex;align-items:center;grid-area:search-container;background:#0048aa;padding:0 20px;border-radius:10px 0 0 0;box-shadow:0 1px 3px -1px rgba(0,0,0,0.74902);z-index:1}#search-container input{width:0;flex:1 0;color:#eee;outline:none;font-weight:bold;border-radius:2px;height:30px;border:0;padding-left:48px;padding-right:20px;font-size:1.4rem;background:url(/react-chat-ui/static/media/search.44d3b27b.svg) no-repeat rgba(255,255,255,0.301961);background-position:15px center;background-size:20px 20px}#search-container input::-webkit-input-placeholder{color:#ddd;font-weight:bold}#search-container input:-ms-input-placeholder{color:#ddd;font-weight:bold}#search-container input::-ms-input-placeholder{color:#ddd;font-weight:bold}#search-container input::placeholder{color:#ddd;font-weight:bold}

.conversation{display:grid;grid-template-columns:40px 1fr -webkit-max-content;grid-template-columns:40px 1fr max-content;grid-gap:10px;color:#fff;font-size:1.1rem;border-bottom:1px solid #003781;padding:20px 20px 20px 15px}.conversation.active,.conversation:hover{background:#003781}.conversation:hover{cursor:pointer}.conversation>img{grid-row:span 2;height:40px;width:40px;border-radius:100%}.conversation .title-text{font-weight:bold;padding-left:5px;white-space:nowrap;overflow-x:hidden;text-overflow:ellipsis}.conversation .created-date{color:#ddd;white-space:nowrap;font-size:1rem}.conversation .conversation-message{grid-column:span 2;padding-left:5px;white-space:nowrap;overflow-x:hidden;text-overflow:ellipsis}

#conversation-list{grid-area:conversation-list;background:#0048aa;overflow-y:scroll}

#new-message-container{display:grid;grid:40px / 40px;align-content:center;grid-area:new-message-container;background:#0048aa;border-top:1px solid #000c1c;border-radius:0 0 0 10px;padding:0 15px}#new-message-container button{display:flex;align-items:center;justify-content:center;background:#eee;border-radius:100%;color:#003781;text-decoration:none;font-size:3.6rem;line-height:3.6rem;outline:none;border:none;cursor:pointer}

.trash-logo{stroke:#0048aa;cursor:pointer}

#chat-title{display:grid;grid:36px / 1fr 36px;align-content:center;align-items:center;grid-area:chat-title;background:#eee;color:#0048aa;font-weight:bold;font-size:2rem;border-radius:0 10px 0 0;box-shadow:0 1px 3px -1px #2e2e2e;padding:0 20px;z-index:1}

.primary-button{flex:0 0 auto;background:#0048aa;border:1px solid #003781;color:#fff;padding:12px;border-radius:5px;font-size:1.4rem;cursor:pointer;outline:none}.primary-button:disabled{background:#778eaf;border:1px solid #747d8f}

.attachment-logo{fill:#0048aa;cursor:pointer;width:32px;height:32px;enable-background:new 0 0 512.001 512.001}

#chat-form{display:flex;align-items:center;grid-area:chat-form;background:#eee;border-radius:0 0 10px 0;border-top:1px solid #cfcfcf;padding-left:42px;padding-right:22px}#chat-form input{flex:1 0;outline:none;padding:15px;border:2px solid #ddd;border-right:none;color:#330;border-radius:6px 0 0 6px;font-size:1.4rem;margin-left:15px}#chat-form button{height:51px;flex:0 0 90px;border-radius:0 6px 6px 0}

#chat-container{display:grid;grid:"search-container chat-title" 71px "conversation-list chat-message-list" 1fr "new-message-container chat-form" 78px / 275px 1fr;min-width:1000px;max-width:1000px;max-height:800px;width:100%;height:95vh;background:#fff;border-radius:10px;-ms-overflow-style:none;scrollbar-width:none;font-family:Arial, Helvetica, sans-serif !important;font-size:10px !important;position:relative;overflow:hidden}#chat-container ::-webkit-scrollbar{display:none}

#no-coversation-layout{flex:1 0;display:flex;flex-direction:column}#no-coversation-layout #no-conversation-content{flex:1 0;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:0 0 10px 10px}#no-coversation-layout h2{color:#0048aa;font-size:3.5rem;margin-bottom:2rem}#no-coversation-layout p{font-size:2rem}#no-coversation-layout p:last-of-type{margin-bottom:1.5rem}

.message-row{display:grid;grid-template-columns:70%;margin-bottom:20px}.message-row>.message-content{display:grid}.message-row>.message-content>img{border-radius:100%;grid-row:span 2;width:48px;height:48px}.message-row>.message-content>.message-time{font-size:1.3rem;color:#777}.message-row>.message-content>.message-text{padding:9px 14px;font-size:1.2rem;margin-bottom:5px}.message-row.you-message{justify-content:end}.message-row.you-message>.message-content{justify-items:end}.message-row.you-message>.message-content>.message-text{background:#0048aa;color:#fff;border:1px solid #0048aa;border-radius:14px 14px 0 14px}.message-row.other-message{justify-items:start}.message-row.other-message>.message-content{grid-template-columns:48px 1fr 20px;grid-column-gap:15px}.message-row.other-message>.message-content>.message-text{background:#eee;color:#111;border:1px solid #ccc;border-radius:14px 14px 14px 0}.message-row.other-message>.message-content>.options{align-self:center;justify-self:center}.message-row.other-message>.message-content>.options>a{font-size:16px;color:#8b8b8c;padding:10px}

#chat-message-list{grid-area:chat-message-list;display:flex;flex-direction:column-reverse;padding:0 20px;overflow-y:scroll;position:relative}

